import {
  GET_NAVBAR,
  REMOVE_NAVBAR,
  GET_CONTENT,
  REMOVE_CONTENT
} from "../types";

export default (state, action) => {
  switch (action.type) {
    case GET_NAVBAR:
      return { ...state, visible: true };
    case REMOVE_NAVBAR:
      return {
        visible: false
      };
    case GET_CONTENT:
      return { ...state, visiblecon: true };
    case REMOVE_CONTENT:
      return {
        visiblecon: false
      };
    default:
      return state;
  }
};
